<template>
  <view class="tasks-all">
    <u-navbar
        isFixed
        :borderBottom="false"
        :customBack="back"
        backIconName="arrow-leftward"
        titleSize="34"
        :background="background"
        title="我的任务"></u-navbar>
    <view class="select-wrapper">
      <nnkjc-capsule-select
          :list="list"
          :current="curNow"
          @change="sectionChange"></nnkjc-capsule-select>
    </view>

    <view class="common-select-wrapper">
      <view>
        <common-select
            :defaultTitle="'按状态'"
            @selecting="statusEmit"
            ref="status">
          <view slot="selectArea">
            <u-select
                :default-value="statusDefaultV"
                mode="single-column"
                v-model="selectingStatus"
                @confirm="statusConfirm"
                @cancel="statusCancel"
                :list="statusList"></u-select>
          </view>
        </common-select>
      </view>

      <view>
<!--        <common-select-->
<!--            :defaultTitle="'日期范围'"-->
<!--            @selecting="statusEmit"-->
<!--            ref="dateRange">-->
<!--          <view slot="selectArea">-->
<!--            <u-select-->
<!--                :default-value="statusDefaultV"-->
<!--                mode="single-column"-->
<!--                v-model="selectingStatus"-->
<!--                @confirm="statusConfirm"-->
<!--                @cancel="statusCancel"-->
<!--                :list="statusList"></u-select>-->
<!--          </view>-->
<!--        </common-select>-->
      </view>
    </view>

    <swiper class="swiper-box" :current="curNow" @transition="transition" @animationfinish="animationfinish">
      <swiper-item class="swiper-item">
        <scroll-view scroll-y style="height: 100%;width: 100%;" @scrolltolower="reachBottom(0)">
          <view class="margin-normal radius-normal bg-white task-body" v-if="taskLists[0].length">
            <nnkjc-task-item
                @single-click="forTaskDetail(task)"
                v-for="(task) in taskLists[0]"
                :taskTitle="task.seqNo"
                :taskTime="task.createTime"
                :taskContent="task.content"
                :iconClass="task.type"
                :taskType="task.type"
                :statusText="task.statusText"
                :overdueFlag="task.overdueFlag"
                :statusFlag="task.statusFlag"
                :key="task.seqNo">
            </nnkjc-task-item>
          </view>
<!--          <u-empty v-show="!taskLists[0].length" :show="true" :text="'暂无' + list[0].name + '任务'" mode="list"></u-empty>-->
        </scroll-view>
      </swiper-item>

      <swiper-item class="swiper-item">
        <scroll-view scroll-y style="height: 100%;width: 100%;" @scrolltolower="reachBottom(1)">
          <view class="margin-normal radius-normal bg-white task-body" v-if="taskLists[1].length">
            <nnkjc-task-item
                @single-click="forTaskDetail(task)"
                v-for="(task) in taskLists[1]"
                :taskTitle="task.seqNo"
                :taskTime="task.createTime"
                :taskContent="task.content"
                :iconClass="task.type"
                :taskType="task.type"
                :statusText="task.statusText"
                :overdueFlag="task.overdueFlag"
                :statusFlag="task.statusFlag"
                :key="task.seqNo">
            </nnkjc-task-item>
          </view>
<!--          <u-empty v-show="!taskLists[1].length" :show="true" :text="'暂无' + list[1].name + '任务'" mode="list"></u-empty>-->
        </scroll-view>
      </swiper-item>

      <swiper-item class="swiper-item">
        <scroll-view scroll-y style="height: 100%;width: 100%;" @scrolltolower="reachBottom(2)">
          <view class="margin-normal radius-normal bg-white task-body" v-if="taskLists[2].length">
            <nnkjc-task-item
                @single-click="forTaskDetail(task)"
                v-for="(task) in taskLists[2]"
                :taskTitle="task.seqNo"
                :taskTime="task.createTime"
                :taskContent="task.content"
                :iconClass="task.type"
                :taskType="task.type"
                :statusText="task.statusText"
                :overdueFlag="task.overdueFlag"
                :statusFlag="task.statusFlag"
                :key="task.seqNo">
            </nnkjc-task-item>
          </view>
<!--          <u-empty v-show="!taskLists[2].length" :show="true" :text="'暂无' + list[2].name + '任务'" mode="list"></u-empty>-->
        </scroll-view>
      </swiper-item>
    </swiper>
    <u-toast ref="uToast"/>
  </view>
</template>

<script>
  import CONFIG from "../../../config"

  export default {
    name: "tasks",
    data() {
      return {
        statusDefaultV: [0],
        selectingStatus: false,  //  是否按照状态选择
        selectingResult: false,  //  是否按照结果选择
        selectingDaterange: false,  //  是否按照日期的范围选择
        empty: false,
        background: {
          'background': '#f2f2f8'
        },
        showStatus: false,
        statusList: [
          {
            value: '',
            label: '全部'
          },
          {
            value: '0',
            label: '正常'
          },
          {
            value: '1',
            label: '异常'
          }
        ],
        searchParams: {
          statusFlag: "",
          endDate: "",
          startDate: ""
        },
        list: [
          {
            pageNum: 1,
            pageSize: 10,
            name: '巡查',
            type: "xuncha",
            empty: false
          },
          {
            pageNum: 1,
            pageSize: 10,
            name: '保养',
            type: "weihu",
            empty: false
          },
          {
            pageNum: 1,
            pageSize: 10,
            name: '维修',
            type: "weixiu",
            empty: false
          }
        ],
        taskLists: [[], [], []],
        curNow: 0
      }
    },
    onReady() {
      this.getTaskListForAll(this.curNow);
    },
    methods: {
      statusCancel(v) {
        this.$refs["status"].selecting = false;
        this.list[this.curNow].pageNum = 1;
      },
      statusConfirm(v) {
        this.$refs["status"].selecting = false;
        this.list[this.curNow].pageNum = 1;
        this.searchParams.statusFlag = v[0].value;
        for (let i = 0; i < this.statusList.length; i++) {
          if (this.statusList[i].value === v[0].value) {
            this.statusDefaultV = [i]
            break;
          }
        }
        this.getTaskListForAll(this.curNow);
      },
      statusEmit(v) {
        this.selectingStatus = v;
      },
      animationfinish({detail: {current}}) {
        this.curNow = current;
        if (!this.taskLists[current].length) {
          this.getTaskListForAll(current);
        }
      },
      transition() {

      },
      reachBottom(index) {
        if(!this.list[index].empty) {
          this.list[index].pageNum++;
          this.getTaskListForAll(index);
        } else {
          this.$refs.uToast.show({
            title: "没有更多数据了~",
            type: "warning"
          });
        }
      },
      back() {
        uni.navigateBack({
          delta: 1
        })
      },
      sectionChange(index) {
        this.curNow = index;
        if (!this.taskLists[index].length) {
          this.getTaskListForAll(index);
        }
      },
      //  任务列表
      getTaskListForAll(index) {
        const {pageSize, pageNum, type} = this.list[index];
        const {statusFlag, endDate, startDate} = this.searchParams;
        this.$u
            .post("/maint/getMyTaskList", {
              pageSize,
              pageNum,
              type,
              statusFlag,
              endDate,
              startDate
            })
            .then(res => {
              if (res.data.rows.length > 0) {
                if(pageNum === 1) {
                  this.taskLists.splice(index, 1, res.data.rows);
                } else {
                  this.taskLists[index].push(...res.data.rows);
                }
              } else {
                if (pageNum == 1) {
                  this.taskLists.splice(index, 1, []);
                } else {
                  this.list[index].pageNum = 1;
                }
              }
              this.list[index].empty = this.taskLists[index].length === res.data.total;
            })
            .catch(err => {
              console.log(err);
            });
      },
      //  维保视图相关页面
      forTaskDetail(task) {
        const params = this.$u.queryParams({
          taskId: task.id,
          status: task.status,
          type: task.type
        })
        uni.navigateTo({
          url: CONFIG.ROUTE_MAP[task.type][task.status] + params
        });
      },
    }
  }
</script>

