  uni-page-body {
    height: 100%;
  }

  .tasks-all {
    height: 100%;
    padding-bottom: 20px;
    display: flex;
    flex-direction: column;
  }

  .select-wrapper {
    margin: 0 25%;
    padding: 5px 0;
  }

  .swiper-box {
    flex-grow: 1;
  }

  .common-select-wrapper {
    margin: 0 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
</style>